.YourButton {
  background-color: lightgray;
  border-radius: 4px;
  cursor: pointer;
  padding: 20px;
  position: relative;
  transition: background-color 0.2s;
}

.YourButton:before {
  background: linear-gradient(to right, lightblue, blue);
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transition: width 0.2s ease;
  width: 0;
}

.YourButton.holding:before {
  width: 100%;
}

.YourButton:active {
  background-color: blue;
}

.YourButton button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
  outline: none;
  padding: 10px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.YourButton.holding {
  overflow: hidden;
}

.YourButton.holding:before {
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: inherit;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}

.YourButton.holding:active:before {
  -webkit-transform: scale(10);
          transform: scale(10);
}


