}
.game-page .game-contents .summary-panel .summary-info .summary-item {
    color: #060607;
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    font-weight: 500;
}
.game-page .game-contents .summary-panel .summary-info .summary-item .summary-item-value {
    color: #fa3fa0;
    font-weight: bold;
    background: #fa3fa029;
    padding: 0px 10.5px;
    border-radius: 6px;
}

.game-page .game-contents h3.game-sub-title {
    font-size: 16px;
    color: #060607;
    line-height: 19px;
    margin: 0 0 15px;
