    margin-top: 16px;
}
.game-page .game-contents .game-info-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
overflow: hidden;
    min-height: 150px;
    padding: 0 20px;
}
.game-page .game-contents .game-info-panel button {
    padding: 0 20px;
    margin: 0 4px 10px;
}
.game-page .game-contents .game-info-panel button.other {
    border-radius: 6px;
}
.game-page .game-contents .game-info-panel button.active {
    color: #e5e5e5;
    background-image: linear-gradient(to top, #48ff00, #07c700);
    box-shadow: 0 -1px 0px #52df00,0 -4px #2d7001 inset,0 2px #07c400 inset;
}
.game-page .game-contents .game-info-panel button:hover {
    transition: 0.1s;
   -webkit-transform: translateY(-2px);
           transform: translateY(-2px);
   -webkit-filter: brightness(1.2);
           filter: brightness(1.2);
}

.game-page .game-contents .game-info-panel button:focus,
.game-page .game-contents .game-info-panel button:active {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    background-image: linear-gradient(to top, #48ff00, #07c700);
box-shadow:none !important;
}
.game-page .game-contents .game-info-panel .edit-amount-panel {
