}

.gradient-container .previous-guesses.drop:before  {
    content: " ";
	width: 80px;
	height: 80px;
	position: absolute;
	z-index: 2;
    -webkit-transform: translate(-5px, -10px);
            transform: translate(-5px, -10px);
	background: linear-gradient(90deg, #151515, transparent);
    }
    
    .gradient-container .previous-guesses.drop {
max-width: 450px;
        overflow-x: scroll;
    }
    .gradient-container .previous-guesses.drop span {
    min-width: 29%;
