table#runs {
    border-radius: 8px;
    width: 300px;
}
table#runs th,
table#runs td {
    padding: 5px 15px;
    text-align: center;
    border-radius: 16px;
}

table#runs td svg {
    cursor: pointer;
}



#runs td svg:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #ffffff5c;
    border-radius: 20px;
    padding: 0.5px;
    transition: 0.2s;
}
#rps-game-type-radio,
#rps-radio {
    text-align: center;
}
#rps-game-type-radio Button {
    text-align: center;
    margin: 0 16px;
    display: inline-block;
    padding-top: 20px;
    width: 200px;
    height: 70px;
    background-position: center;
    border-radius: 12px;
    -webkit-animation: pulse 0.2s;
            animation: pulse 0.2s;

}



#rps-radio Button {
    background-size: contain;
    background-repeat: no-repeat;
    display: inline-block;
    width: 130px;
    height: 200px !important;
    cursor: pointer;
    box-shadow: 0 -1px 0px #045308,0 -4px #50ee2c inset,0 2px #12c526 inset!important;
    background-color: #03c927;
    -webkit-filter: drop-shadow(2px 4px 6px black);
            filter: drop-shadow(2px 4px 6px black);
    transition: all 0.2s ease-in-out;
    position: relative;
    border-radius: 0.25em !important;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    }

    #rps-radio Button:hover {
        -webkit-filter: brightness(1.2);
                filter: brightness(1.2);
        -webkit-transform: translateY(-4px);
                transform: translateY(-4px);
        -webkit-animation: pulse 0.2s ease-in-out;
                animation: pulse 0.2s ease-in-out;
    }
    @-webkit-keyframes pulse {
        0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        }
        50% {
        -webkit-transform: scale(1.02);
                transform: scale(1.02);
        }
        100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        }
        }
    @keyframes pulse {
        0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        }
        50% {
        -webkit-transform: scale(1.02);
                transform: scale(1.02);
        }
        100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        }
        }
    #rps-radio Button:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 12px;
    box-shadow: inset 0 0 40px rgba(0, 0, 0, 0.1);
    -webkit-transform: rotateX(90deg) translateZ(25px);
            transform: rotateX(90deg) translateZ(25px);
    opacity: 0;
    transition: all 0.2s ease-in-out;
    }

    #rps-radio Button.active,#rps-radio Button:active  {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        background-color: #049c20;
        box-shadow: none !important;
    }
    
    #rps-radio Button:hover:before {
    opacity: 1;
    -webkit-transform: rotateX(0deg) translateZ(25px);
            transform: rotateX(0deg) translateZ(25px);
    }
  
