.game-page .game-contents .action-panel button,
#btn-signup,
button.btn-submit,
.send-msg
{
    height: 40px;
    margin: 0 15px;
    background-image: linear-gradient(to top, #ff409a, #c438ef);
    color: white;
    border-radius: 0.25em;
    padding: 0 20px;
    font-weight: 600;
    
}
.create-game-panel .game-page .game-contents .action-panel
    {
    display: flex;
    justify-content: space-between;
}
.create-game-panel .game-page .game-contents .action-panel button,
#btn-signup,
.send-msg,
button.btn-submit {
    box-shadow: 0 -0.5px 0px #ff409a,0 -3px #c438ef inset,0 2px #e438ef inset!important;
}
.create-game-panel .game-page .game-contents .action-panel button:hover,
.send-msg:hover,
#btn-signup:hover,
button.btn-submit:hover {
-webkit-transform: translateY(-1px);
        transform: translateY(-1px);
-webkit-filter: brightness(1.06);
        filter: brightness(1.06);
transition: 0.2s;
}
.create-game-panel .game-page .game-contents .action-panel button:active,
#btn-signup:active,
button.btn-submit:active,
.send-msg:active {
    background-color: #ff409a;
    box-shadow: none!important;
}


.game-page .game-contents .radio-button-group button.radio-button {
    background-color: rgba(242, 243, 245, 0.4);
    border-radius: 8px;
    height: 40px;
    color: #4f5660;
    font-size: 16px;
    font-weight: normal;
    margin-right: 24px;
    border: 0;
}
.game-page .game-contents .radio-button-group button.radio-button::before {
    width: 23px;
    height: 23px;
    content: ' ';
    display: inline-block;
    margin-right: 10px;
    vertical-align: middle;
    border-radius: 50%;
    border: 2px solid #747f8d;
}
.game-page .game-contents .radio-button-group button.radio-button.selected::before {
    background-color: #e9ecef;
    border: 2px solid #e9ecef;
    box-shadow: 0 0 0px 7px #fff inset;
}

#btn-rps-settings {
    position: absolute;
    right: 0;
    cursor: pointer;
    z-index: 1;
}
.game-page .pre-summary-panel .transaction-settings {
    position: absolute;
    right: 0;
    top: 70px;
    padding: 20px 30px;
    background: #2f3136;
    border-radius: 8px;
    opacity: 0;
    transition: .2s;
}
.game-page .pre-summary-panel .transaction-settings.active {
    opacity: 1;
    z-index: 999;
}
input {
    border-radius: 8px !important;
    outline: 0;
    padding-left: 8px;
    border-width: 1px;
}


