.create-game-panel .boxes-panel {
    grid-gap: 0.4rem;
    grid-template-columns: repeat(6, 1fr);
}

.boxes-panel {
    grid-gap: 0.6rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}

.boxes-panel .box i {
    width: 20px;
    z-index: 99;
    height: 20px;
    background-color: #ff0000;
    border-radius: 50%;
    color: white;
    line-height: 18px;
    font-size: 20px;
    text-align: center;
    position: absolute;
    right: -10px;
    bottom: 15px;
    cursor: pointer;
}
.create-game-panel .boxes-panel .box span {
    background: rgba(0,0,0,0.2);
    font-size: 0.8EM;
}
.boxes-panel .box span {
    color: #444;
    font-size: 11px;
    line-height: 30px;
    font-weight: bold;
    text-align: center;
    width: 100%;
    display: table;
    letter-spacing: 0.05em;
    border-radius: 9px;
    padding: 4px;
    margin-top: -10px;
}

.boxes-panel .box.active span {
    /* background: #c438ef0f; */
    color: #fa3fa0;
}

.boxes-panel .box.active {
    box-shadow: unset !important;
    transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.boxes-panel.boxes-join .box {
    background-position: center 15px;
    border-radius: 6px;
    cursor: pointer;
    border: unset !important;
    background-size: 2.7em;
    background-color: #15ea21;
}


.create-game-panel .boxes-panel .box {
    background-size: 3.5em;
